body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
}

.rightCol {
  width: 80%;
  float: left;
  text-align: left;
}

.filterBtn {
  display: none;
}

.SelectedFilterBox .selectedFilter {
  background: #9e9e9e;
  border-radius: 5px;
  padding: 5px;
  display: inline-block;
  margin-right: 5px;
  color: #fff;
  font-size: 12px;
}

.SelectedFilterBox .selectedFilter span {
  cursor: pointer;
}

.sort {
  text-align: right;
  margin: 20px 0 30px;
}

.sort select {
  display: inline-block;
  outline: none;
  width: 200px;
}

.tile-outer {
  width: 25%;
  max-width: 400px;
  padding: 10px 5px 5px;
  background: #202329;
  float: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.tile {
  -webkit-box-shadow: rgba(0,0,0,0.16) 0px 2px 2px;
          box-shadow: rgba(0,0,0,0.16) 0px 2px 2px;
  border-radius: 10px;
  overflow: hidden;
}

.tile .imgbox {
  position: relative;
}

.tile .imgbox img {
  width: 100%;
}

.tile .imgbox .nameIdBox {
  background: rgba(32,35,41,0.8);
  padding: 10px;
  text-align: left;
  position: absolute;
  bottom: 0;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.tile .imgbox .nameIdBox h2 {
  margin: 0;
  font-size: 24px;
  color: #f5f5f5;
}

.tile .imgbox .nameIdBox h2 span {
  display: block;
  font-size: 12px;
  color: #bbbbbb;
  font-weight: 500;
}

.tile .textbox {
  background: #333;
  padding: 10px;
}

.tile .textbox .row {
  border-bottom: 1px solid #444;
  font-size: 12px;
  padding: 5px 0 10px;
}

.tile .textbox .row .col1 {
  width: 25%;
  color: #9e9e9e;
  text-align: left;
  display: inline-block;
}

.tile .textbox .row .col2 {
  width: 75%;
  text-align: right;
  color: #ff9800;
  display: inline-block;
  padding-left: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.tile .textbox .row .col2.ellipsis1 {
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.leftCol {
  width: 20%;
  float: left;
  text-align: left;
  padding-right: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.leftCol h2 {
  margin: 20px 0;
}

.leftCol .close-filter {
  float: right;
  display: none;
  cursor: pointer;
  background: url(/static/media/close.f21e692a.svg);
  width: 20px;
  height: 20px;
  margin-top: 6px;
}

.leftCol .filterBox {
  width: 100%;
  padding: 0 10px;
  border: 1px solid #ccc;
  margin-bottom: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.leftCol .filterBox h3 {
  margin: 10px 0;
}

.leftCol .filterBox label {
  display: block;
  margin-bottom: 10px;
  font-size: 12px;
}

@media only screen and (max-width: 767px) {
  .App {
    width: 100%;
    padding: 0 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }

  .leftCol,
  .rightCol {
    width: 100%;
    padding: 0;
  }

  .leftCol {
    background: #fff;
    position: fixed;
    z-index: 2;
    left: 0;
    top: 0;
    padding: 0 10px;
    display: none;
    height: 100vh;
    overflow-y: auto;
  }

  .leftCol.active {
    display: block;
  }

  .leftCol .close-filter {
    display: block;
  }

  .sort {
    width: 100%;
  }

  .sort select {
    width: 100%;
  }

  .tile-outer {
    width: calc(50% - 5px);
    margin: 0 10px 10px 0;
  }

  .tile-outer:nth-child(odd) {
    margin: 0 0 10px 0;
  }

  .tile .imgbox .nameIdBox {
    padding: 5px;
  }

  .tile .imgbox .nameIdBox h2 {
    font-size: 16px;
  }

  .tile .imgbox .nameIdBox h2 span {
    font-size: 12px;
  }

  .filterBtn {
    display: block;
    position: fixed;
    z-index: 1;
    bottom: 20px;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    background: #fff;
    padding: 10px 20px;
    border-radius: 20px;
    font-size: 16px;
    -webkit-box-shadow: 0 2px 12px 0 rgba(114,114,114,0.5);
            box-shadow: 0 2px 12px 0 rgba(114,114,114,0.5);
    line-height: 20px;
  }

  .filterBtn .filter-icon {
    background: url(/static/media/filter.49eb9564.svg);
    width: 20px;
    height: 20px;
    display: inline-block;
    float: left;
    margin-right: 5px;
  }
}

@media only screen and (max-width: 400px) {
  .tile-outer {
    width: 100%;
    margin: 0;
  }
}

